.syllabus_tools_container {
    display: flex;
    flex-direction: row;
}
.syllabus_info .syllabus_info_select {
    display: flex;
}
.syllabus_compare_contents {
    display: flex;
    margin-top: 10px;
    margin-left: 10px;
}
.syllabus_compare_sidebar {
    width: 10%;
}
.syllabus_compare_splitpane {
    width: 90%;
}
.syllabus_tools {
    display: flex;
    width: 85%;
}
.syllabus_tools_btn {
    position: relative;
}
.syllabus_tools_btn button {
    position: absolute;
    bottom: 0;
    width: 100px;
}
.split_view_container {
    padding: 0 10px;
}
